<style lang="scss">
.card {
  width: 320px !important;
  margin: 0 auto !important;
  .ui[class*="left icon"].input>input {
    text-align: center;
    padding-left: 3em !important;
  }
  .time, .users {
    border-right: 1px solid rgba(34,36,38,.15) !important;
  }
  .statistics {
    margin: 2em -1em 0em !important;
    justify-content: space-around;
    &:after {
      content: none !important;
    }
    .statistic {
      margin: 0 !important;
    }
  }
}
</style>

<template>
  <div class="ui card">
    <sui-progress attached top indicating :percent="percent"></sui-progress>

    <div class="extra content">
      <div class="header">
        <a :href="miner.href" target="_blank">{{ this.index + 1}}. {{ this.miner.name }}, charge {{ this.miner.charge }}%</a>
      </div>
    </div>

    <div class="content">
      <div class="ui form">
        <div class="">
          <div class="field">
            <p>Sessions per month</p>
            <div class="ui left icon input massive">
              <i class="users icon dop-icon"></i>
              <vue-numeric v-model="users"></vue-numeric>
            </div>
          </div>
          <div class="field">
            <p>Average visit duration (seconds)</p>
            <div class="ui left icon input massive">
              <i class="time icon dop-icon"></i>
              <vue-numeric v-model="time"></vue-numeric>
            </div>
          </div>
        </div>
      </div>
      <div class="ui statistics small">
        <div class="statistic">
          <div class="value">{{ percent + '%' }}</div>
          <div class="label">CPU usage</div>
        </div>
        <div class="statistic">
          <div class="value">{{ usdmonth.toFixed(2) }}</div>
          <div class="label">usd / month</div>
        </div>
      </div>
    </div>
    <div class="ui icon buttons">
      <sui-button @click.native="decrease">
        <i class="minus icon"></i>
      </sui-button>
      <sui-button @click.native="backward">
        <i class="icon backward"></i>
      </sui-button>
      <sui-button @click.native="forward">
        <i class="icon forward"></i>
      </sui-button>
      <sui-button @click.native="increase">
        <i class="plus icon"></i>
      </sui-button>
    </div>
  </div>
</template>

<script>
export default {
  props: ['miners', 'xmrusd', 'reward', 'hashes'],
  data() {
    return {
      index: 0,
      time: 300,
      users: 1000000,
      percent: 70,
    }
  },
  computed: {
    miner() {
      return this.miners[this.index]
    },
    usdmonth() {
      const hashes_in_month = this.users * this.time * this.hashes * (this.percent / 100)
      const reward_in_month = hashes_in_month * this.reward
      const reward_after_charge = reward_in_month * (100 - this.miner.charge) / 100
      return reward_after_charge * this.xmrusd
    },
  },
  methods: {
    backward() {
      this.index = this.index > 0 ? this.index - 1 : this.miners.length - 1
    },
    forward() {
      this.index = (this.index + 1) % this.miners.length
    },
    increase() {
      if (this.percent < 100) {
        this.percent += 10;
        if (this.loaded) {
          this.miner.setThrottle(this.throttle)
        }
      }
    },
    decrease() {
      if (this.percent > 10) {
        this.percent -= 10;
        if (this.loaded) {
          this.miner.setThrottle(this.throttle)
        }
      }
    },
  }
}
</script>


