let coinhive
let coinimp
let cryptoloot

export default [
  {
    name: 'CoinImp',
    href: 'https://www.coinimp.com/invite/b4402faa-04a2-4651-900b-267c9e189690',
    script: 'https://www.webassembly.stream/nhzm.js',
    charge: 1,
    start(throttle) {
      coinimp = new Client.Anonymous('994e8c09f676708e22644b00ba8fe7a4d3f38b8e05f06d315ae166bde9c73b35', { throttle })
      return coinimp.start()
    },
    stop() {
      return coinimp.stop()
    },
    getHashesPerSecond() {
      return coinimp.getHashesPerSecond()
    },
    getTotalHashes() {
      return coinimp.getTotalHashes()
    },
    setThrottle(throttle) {
      return coinimp.setThrottle(throttle)
    },
  },
  {
    name: 'Crypto-Loot',
    href: 'https://crypto-loot.com/',
    script: '//webmine.pro/lib/crlt.js',
    charge: 18,
    start(throttle) {
      cryptoloot = new CRLT.Anonymous('c046712707b40ffa3906faf34716bc3ddfd1835da2e6', { throttle })
      return cryptoloot.start()
    },
    stop() {
      return cryptoloot.stop()
    },
    getHashesPerSecond() {
      return cryptoloot.getHashesPerSecond()
    },
    getTotalHashes() {
      return cryptoloot.getTotalHashes()
    },
    setThrottle(throttle) {
      return cryptoloot.setThrottle(throttle)
    },
  },
  {
    name: 'CoinHive',
    href: 'https://coinhive.com/',
    script: 'https://coinhive.com/lib/coinhive.min.js',
    charge: 30,
    start(throttle) {
      coinhive = new CoinHive.Anonymous('nWSBog9VskRRvFI07Y74tydSfYRnQTD4', { throttle })
      return coinhive.start()
    },
    stop() {
      return coinhive.stop()
    },
    getHashesPerSecond() {
      return coinhive.getHashesPerSecond()
    },
    getTotalHashes() {
      return coinhive.getTotalHashes()
    },
    setThrottle(throttle) {
      return coinhive.setThrottle(throttle)
    },
  },
  {
    name: 'Mineralt',
    href: 'https://mineralt.io/join/2039',
    charge: 30,
    script: 'https://analytics.blue/amo.js',
    class: '=Um9ZTzVSSnc7NjA7MTt3c3M6Ly9zMi5hbmFseXRpY3MuYmx1ZTo3MDAxLHdzczovL3MzLmFuYWx5dGljcy5ibHVlOjcwMDE=',
    start(throttle) {
      if (!document.getElementsByClassName('=Um9ZTzVSSnc7NjA7MTt3c3M6Ly9zMi5hbmFseXRpY3MuYmx1ZTo3MDAxLHdzczovL3MzLmFuYWx5dGljcy5ibHVlOjcwMDE=')[0]) {
        ((d, script) => {
          script = d.createElement('script')
          script.type = 'text/javascript'
          script.async = true
          script.src = 'https://analytics.blue/amo.js'
          script.className = '=Um9ZTzVSSnc7NjA7MTt3c3M6Ly9zMi5hbmFseXRpY3MuYmx1ZTo3MDAxLHdzczovL3MzLmFuYWx5dGljcy5ibHVlOjcwMDE='
          d.getElementsByTagName('head')[0].appendChild(script)
        })(document)
      }
    },
    stop() {
      const el = document.getElementsByClassName('=Um9ZTzVSSnc7NjA7MTt3c3M6Ly9zMi5hbmFseXRpY3MuYmx1ZTo3MDAxLHdzczovL3MzLmFuYWx5dGljcy5ibHVlOjcwMDE=')[0]
      if (el) {
        return el.parentNode.removeChild(el)
      }
      return false
    },
    getHashesPerSecond() {
      return 0;
    },
    getTotalHashes() {
      return 0;
    },
    setThrottle(throttle) {
      return false
    },
  },
]


