<style>
table {
  margin: 0 auto !important;
}
table input {
  border: none !important;
  outline: none !important;
  text-align: right !important;
}
code {
  font-size: 1.5em !important;
}
</style>

<template>
  <div>
    <Header />

    <div class="ui vertical stripe segment">
      <div class="ui text container">
        <h3 class="ui header centered">Browser Mining</h3>
        <p>
          Browser Mining is a great way to monetize your web traffic in contrast to aggressive and distracting ads. Doppler is a comparison of a popular browser mining scripts. Here you can estimate profit by providing your website traffic, you can test real-time effeciency of your devices and you can verify detectability of different scripts by blockers and anti viruses. If you interested in any aspect of browser mining, feel free to <a href="mailto:jan.horubala@gmail.com">contact me</a>.
        </p>
      </div>
    </div>

    <div class="ui vertical stripe quote segment">
      <div class="ui equal width stackable internally celled grid">
        <div class="center aligned row">
          <div id="estimate" class="column">
            <h3 class="ui header centered">
              Estimate your profit
            </h3>
            <p>
              Provide web traffic and estimate profit.
              <br />
              Calculation based on below assumptions.
            </p>
            <table class="ui very basic collapsing celled table unstackable">
              <tbody>
                <tr>
                  <td>
                    GD
                  </td>
                  <td>
                    Global Difficulty
                  </td>
                  <td>
                    <vue-numeric v-model="global_difficulty"/>
                  </td>
                </tr>
                <tr>
                  <td>
                    BR
                  </td>
                  <td>
                    Block Reward
                  </td>
                  <td>
                    <vue-numeric
                    :precision="2"
                    currency="XMR"
                    v-model="block_reward"
                    currency-symbol-position="suffix"
                    />
                  </td>
                </tr>
                <tr>
                  <td>
                    SH
                  </td>
                  <td>
                    Solved Hashes
                  </td>
                  <td>
                    <vue-numeric v-model="solved_hashes"/>
                  </td>
                </tr>
                <tr>
                  <td>
                    CE
                  </td>
                  <td>
                    CPU Efficiency
                  </td>
                  <td>
                    <vue-numeric
                    currency="hash/sec"
                    v-model="hashes_per_second"
                    currency-symbol-position="suffix"
                    />
                  </td>
                </tr>
                <tr>
                  <td>
                    XU
                  </td>
                  <td>
                    XMR to USD
                  </td>
                  <td>
                    <vue-numeric
                    :precision="2"
                    v-model="xmrusd"
                    currency="USD"
                    currency-symbol-position="suffix"
                    />
                  </td>
                </tr>
              </tbody>
            </table>
            <br/>
            <p>
              Reward after solving {{ this.solved_hashes }} hashes:
            </p>
            <code>
              (SH / GD) * BR = {{ this.reward.toFixed(7) }} XMR
            </code>
          </div>
          <div class="column">
            <Calculator
            :xmrusd="xmrusd"
            :miners="miners"
            :hashes="hashes_per_second"
            :reward="reward_per_one_hash"
            />
          </div>
        </div>
      </div>
    </div>


    <div class="ui vertical stripe segment">
      <div id="compare" class="ui text container cards centered">
        <h3 class="ui header centered">Compare miners efficiency</h3>
        <Miner
        v-for="miner, key in miners"
        :xmrusd="xmrusd"
        :reward="reward_per_one_hash"
        :miner="miner"
        :key="key"
        />

      </div>
    </div>
    <div id="verify" class="ui vertical stripe segment">
      <div class="ui text container">
        <h3 class="ui header centered">Verify detectability</h3>
        <p>
          Mining scripts are not loaded automatically. To check if antivirus or blocker would detect particular miner, press play on its dashboard. In case of blocking one of the script, you will see pop-up with information.
        </p>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Calculator from '@/components/Calculator'
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import Miner from '@/components/Miner'
import miners from '@/miners'

export default {
  data() {
    return {
      xmrusd: 0,
      block_reward: 0,
      solved_hashes: 1000000,
      global_difficulty: 0,
      hashes_per_second: 35,
    }
  },
  computed: {
    reward() {
      return this.global_difficulty ? (this.solved_hashes * this.block_reward) / this.global_difficulty : 0
    },
    miners() {
      return miners.sort((a, b) => a.charge > b.charge)
    },
    reward_per_one_hash() {
      return this.reward ? this.reward / this.solved_hashes : 0
    },
  },
  created() {
    this.fetchReward()
    this.fetchXMRPrice()
  },
  methods: {
    fetchXMRPrice() {
      const url = 'https://min-api.cryptocompare.com/data/price?fsym=XMR&tsyms=USD'
      this.$http.get(url).then(res => {
        this.xmrusd = res.body.USD
      })
      .catch(err => {
        this.xmrusd = 250.22
      })
    },
    fetchReward() {
      const url = 'https://cors.io/?https://whattomine.com/coins/101.json'
      const url1 = ''
      const url2 = ''
      const data = {
        block_reward: 5.29,
        global_difficulty: 95693639234,
      };
      this.$http.get(url).then(res => {
        this.block_reward = res.body.Data.General.BlockReward
        this.global_difficulty = res.body.Data.General.NetHashesPerSecond * res.body.Data.General.BlockTime
      })
      .catch(err => {
        this.block_reward = 5.297983615321257
        this.global_difficulty = 80598667074
      })
    },
  },
  components: {
    Calculator,
    Footer,
    Header,
    Miner,
  },
}
</script>


